export default {
  layout: 'single-container',
  name: "Signup",
  data() {
    return {
      email:'',
      eye1:false,
      eye2:false,
      word1:'',
      word2:'',
      code:'',
      daley:0,
      deleyTimeId:null,
      isError:false,
    }
  },
  computed:{
    btnDisable(){
      var reg = new RegExp("^[a-zA-Z0-9]+([._\\-]*[A-Za-z0-9])*@([a-z0-9]+[-a-z0-9]*[a-z0-9]+.){1,63}[a-z0-9]+$"); //正则表达式
      let wordReg = new RegExp('^(?![0-9]+$)(?![a-zA-Z]+$)[0-9A-Za-z]{6,16}$');
      if(!this.email || !reg.test(this.email))
       return true;
      if(!wordReg.test(this.word1) || !wordReg.test(this.word2))
        return true;
      if(this.word2 != this.word1)
        return true;
      if(!this.code || this.code.length < 6)
        return true;
      return false;
    }
  },
  beforeDestroy() {
    clearInterval(this.deleyTimeId);
  },
  mounted() {
  },
  methods: {
    onBlur(){
      let wordReg = new RegExp('^(?![0-9]+$)(?![a-zA-Z]+$)[0-9A-Za-z]{6,16}$');
      if(!wordReg.test(this.word1)){
        this.isError = true;
        return;
      }
      this.isError = false;
    },
    onClose() {
      this.regesterSuc=false;
      this.$router.replace('/');
    },
    showPassword1(){
      this.eye1 = !this.eye1;
    },
    async resetPassword(){
      let data =  await this.$axios.post('/v1/auth/ResetPassword', {
        iSource:1,
        iRetrieveType:2,
        sPassword:this.word1,
        sEmail:this.email,
        sEmailVerifyCode:this.code,
      })
      this.$toast('修改成功');
      this.$router.back();
    },
    showPassword2(){
      this.eye2 = !this.eye2;
    },
    async getCode(){
      var reg = new RegExp("^[a-z0-9]+([._\\-]*[a-z0-9])*@([a-z0-9]+[-a-z0-9]*[a-z0-9]+.){1,63}[a-z0-9]+$"); //正则表达式
      if(!this.email || !reg.test(this.email))
        return;
      await this.$axios.post('/v1/auth/SendEmailVerifyCode', {
        iSource:1,
        iSendMethod:2,
        iVerifyBusi:4,
        sEmail:this.email,
      })

      this.daley = 60;
      this.deleyTimeId = setInterval(() => {
        this.daley--;
        if(this.daley <=0){
          clearInterval(this.deleyTimeId);
        }
      }, 1000);
    }
  }
}
